// employees

.employees {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: -12px;
		left: 10px;
		width: 2px;
		height: 105%;
		background-color: @page-dark-grey;
	}

	&::after {
		content: "";
		position: absolute;
		top: -12px;
		left: 0;
		width: 10px;
		height: 2px;
		background-color: @page-dark-grey;
	}

	@media (min-width: @L-min) {
		.slider-arrows {
			position: absolute;
			bottom: 110px;
			left: 60px;
		}

		&::before {
			top: 14px;
			left: 298px;
			height: 98.8%;
		}

		&::after {
			top: 14px;
			width: 298px;
		}
	}
}

.employees__content {
	padding-top: 10px;
	padding-bottom: 90px;

	@media (min-width: @S-min) {
		padding-top: 34px;
		padding-bottom: 30px;
	}

	@media (min-width: @L-min) {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		padding-top: 57px;
		padding-bottom: 49px;
	}
}

.employees__title {
	margin-bottom: 29px;
	padding-left: 10px;

	@media (min-width: @L-min) {
		margin-bottom: 102px;
		padding-left: 79px;
		text-align: center;
	}
}

.employees__features {
	position: relative;
	z-index: 101;
	margin-top: -40px;
	margin-right: -20px;
	padding-top: 29px;
	padding-right: 20px;
	padding-bottom: 30px;
	padding-left: 18px;
	background-color: @basic-white;

	@media (min-width: @M-min) {
		margin-right: -30px;
	}

	@media (min-width: @L-min) {
		width: 470px;
		align-self: flex-end;
		margin-top: -629px;
		margin-right: -60px;
		padding-top: 46px;
		padding-right: 60px;
		padding-bottom: 60px;
		padding-left: 49px;
	}
}
