// projects

.projects {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 10px;
		width: 100%;
		height: 97%;
		border: 2px solid @page-dark-grey;
		border-top-color: transparent;
		border-right-color: transparent;
	}

	@media (min-width: @L-min) {
		.slider-arrows {
			margin-top: -127px;
			margin-bottom: 112px;
			margin-left: auto;
		}

		&::before {
			left: 298px;
		}
	}
}

.projects__content {
	padding-top: 14px;
	padding-bottom: 42px;

	@media (min-width: @S-min) {
		padding-top: 34px;
		padding-bottom: 30px;
	}

	@media (min-width: @M-min) {
		padding-top: 48px;
		padding-bottom: 48px;
	}

	@media (min-width: @L-min) {
		position: relative;
		padding-bottom: 75px;
	}
}

.projects__info {
	margin-bottom: 23px;
	padding-left: 10px;

	@media (min-width: @S-min) {
		width: 80%;
		margin-left: auto;
		padding-left: 0;

		&:last-child {
			margin-bottom: 0;
		}
	}

	@media (min-width: @L-min) {
		width: 860px;
		margin-bottom: 55px;
		padding-right: 150px;
	}
}

.projects__info--cloud {
	position: relative;
	z-index: 101;
	width: calc(100% + 20px);
	margin-top: -30px;
	margin-right: -20px;
	margin-bottom: 25px;
	padding-top: 24px;
	padding-right: 32px;
	padding-bottom: 23px;
	padding-left: 20px;
	background-color: @basic-white;

	@media (min-width: @S-min) {
		width: calc(100% + 20px);
		padding-left: 20px;
	}

	@media (min-width: @L-min) {
		width: 470px;
		min-height: 390px;
		margin: 0;
		margin-top: -518px;
		margin-right: -60px;
		margin-bottom: 177px;
		margin-left: auto;
		padding-top: 58px;
		padding-right: 65px;
		padding-bottom: 57px;
		padding-left: 51px;
	}
}

.projects__title {
	margin-bottom: 14px;

	@media (min-width: @L-min) {
		margin-bottom: 17px;
	}

	@media (min-width: @L-min) {
	}
}

.projects__br {
	@media (min-width: @S-min) {
		display: none;
	}

	@media (min-width: @L-min) {
	}
}

.projects__br--desktop {
	@media (min-width: @L-min) {
		display: inline;
	}
}

.projects__subtitle {
	margin-bottom: 8px;

	@media (min-width: @L-min) {
		margin-bottom: 10px;
	}
}
