// header

.header {
	position: relative;

	@media (min-width: @L-min) {
		&::before {
			content: "";
			position: absolute;
			top: 40px;
			left: 298px;
			width: 2px;
			height: 80%;
			background-color: @page-dark-grey;
		}
	}
}

.header__content {
	padding-top: 24px;
	padding-bottom: 31px;

	@media (min-width: @M-min) {
		padding-top: 40px;
	}

	@media (min-width: @L-min) {
		padding-top: 88px;
		padding-bottom: 40px;
	}
}

.header__head {
	@media (min-width: @S-min) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}
}

.header__logo-img {
	@media (min-width: @L-min) {
		width: 150px;
		height: 40px;
	}
}
