// about-company

.about-company {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 10px;
		width: 2px;
		height: 100%;
		background-color: @page-dark-grey;
	}

	@media (min-width: @L-min) {
		&::before {
			left: 298px;
		}
	}
}

.about-company__content {
	padding-bottom: 20px;
	padding-right: 20px;

	@media (min-width: @M-min) {
		padding-top: 0;
	}

	@media (min-width: @L-min) {
		padding-bottom: 115px;
	}
}

.about-company__image-wrap {
	margin-right: -20px;
	margin-left: 20px;

	@media (min-width: @S-min) {
		width: 70%;
		margin-left: auto;
	}

	@media (min-width: @L-min) {
		width: 920px;
		height: 570px;
	}
}

.about-company__img {
	width: 100%;
}

.about-company__info {
	position: relative;
	z-index: 101;
	margin-top: -40px;
	padding-top: 34px;
	padding-right: 20px;
	padding-bottom: 39px;
	padding-left: 20px;
	background-color: @basic-white;

	@media (min-width: @S-min) {
		width: 50%;
		margin-top: -235px;
		padding-bottom: 20px;
	}

	@media (min-width: @M-min) {
		margin-top: -350px;
		padding-bottom: 45px;
	}

	@media (min-width: @L-min) {
		width: 550px;
		margin-top: -505px;
		padding-top: 49px;
		padding-right: 30px;
		padding-bottom: 53px;
		padding-left: 60px;
	}
}

.about-company__title {
	margin-bottom: 14px;

	@media (min-width: @L-min) {
		margin-bottom: 17px;
	}
}

.about-company__desc {
	margin-bottom: 8px;

	&:last-child {
		margin-bottom: 0;
	}

	@media (min-width: @L-min) {
		margin-bottom: 10px;
	}
}
