// principles

.principles {
	@media (min-width: @L-min) {
		position: relative;

		.slider-arrows {
			position: absolute;
			z-index: 101;
			top: 199px;
			right: 60px;
		}
	}
}

.principles__content {
	padding-top: 34px;
	padding-bottom: 30px;

	@media (min-width: @S-min) {
		padding-top: 34px;
		padding-bottom: 60px;
	}

	@media (min-width: @L-min) {
		padding-top: 61px;
		padding-bottom: 41px;
	}
}

.principles__title {
	margin-bottom: 28px;

	@media (min-width: @M-min) {
		margin-bottom: 40px;
	}

	@media (min-width: @L-min) {
		margin-bottom: 53px;
	}
}
