// page

.page {
	position: relative;
	overflow: hidden;
	padding-right: 15px;
	padding-left: 15px;

	@media (min-width: @M-min) {
		padding-right: 30px;
		padding-left: 30px;
	}

	@media (min-width: @L-min) {
		width: 1250px;
		.centered();
		padding: 0;
		padding-bottom: 115px;
	}
}

.page__main {
	background-color: @page-grey;
}
