/* Выставляем основные настройки по-умолчанию для body */

body {
  min-width: 300px;
  min-height: 100vh;
  overflow-y: scroll;
  font-family: "Arial", sans-serif;
  font-weight: 400;
}

/* Упрощаем работу с изображениями */

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-container-pointer-events {
  touch-action: pan-y;
}

.swiper-container-pointer-events.swiper-container-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */

.swiper-container-3d {
  perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

._content {
  padding-right: 20px;
  padding-left: 20px;
}

._head {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

._logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page {
  position: relative;
  overflow: hidden;
  padding-right: 15px;
  padding-left: 15px;
}

.page__main {
  background-color: #f5f0f0;
}

.page-title {
  color: #4764b0;
  font-size: 24px;
  line-height: 1.166;
  font-weight: 700;
}

.page-title--sub {
  color: violet;
}

.page-subtitle {
  position: relative;
  color: #1e1e1e;
  font-size: 14px;
  line-height: 1.42;
  font-weight: 400;
}

.page-text {
  color: #1e1e1e;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.page-cloud-list {
  margin: 0;
  padding: 0;
  padding-left: 20px;
  list-style: none;
}

.page-cloud-list__item {
  position: relative;
  margin-bottom: 8px;
}

.page-cloud-list__item:last-child {
  margin-bottom: 0;
}

.page-cloud-list__item::before {
  content: "";
  position: absolute;
  top: 9px;
  left: -20px;
  width: 13px;
  height: 1px;
  background-color: #4764b0;
}

.slider {
  position: relative;
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.slider--works {
  margin-left: -20px;
}

.slider--works .slider__dots {
  bottom: 38px;
  left: 50%;
  width: 150px;
  transform: translateX(-50%);
}

.slider--works .swiper-pagination-bullet {
  background-color: #ffffff;
}

.slider--works .swiper-pagination-bullet-active {
  background-color: #4764b0;
  transform: scale(1.8);
}

.slider--principles {
  width: calc(100% + 20px);
  margin-right: -20px;
}

.slider--principles .slider__list {
  height: auto;
}

.slider--principles .slider__item {
  padding-top: 33px;
  padding-right: 20px;
  padding-bottom: 42px;
  padding-left: 20px;
  background-color: #ffffff;
}

.slider--principles .slider__dots {
  bottom: 20px;
  left: 50%;
  width: 70px;
  transform: translateX(-50%);
}

.slider--principles .swiper-pagination-bullet {
  background-color: #4764b0;
}

.slider--principles .swiper-pagination-bullet:hover {
  transform: scale(1.5);
}

.slider--principles .swiper-pagination-bullet-active {
  transform: scale(1.8);
}

.slider--employees {
  width: calc(100% + 20px);
  margin-left: -20px;
}

.slider--employees .slider__dots {
  left: 50%;
  bottom: 50px;
  width: 150px;
  transform: translate(-50%);
}

.slider--employees .swiper-pagination-bullet {
  background-color: #ffffff;
}

.slider--employees .swiper-pagination-bullet-active {
  background-color: #4764b0;
  transform: scale(1.8);
}

.slider__img {
  width: 100%;
}

.slider__subtitle {
  position: relative;
  margin-bottom: 34px;
}

.slider__subtitle::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  width: 100%;
  height: 4px;
  background-color: #4764b0;
}

.slider__subtitle::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 6px;
  width: 6px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 22px 0 0 0 #ffffff, 48px 0 0 0 #ffffff, 77px 0 0 0 #ffffff, 130px 0 0 0 #ffffff;
}

.slider__dots {
  position: absolute;
  z-index: 101;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  min-height: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.slider__dots .swiper-pagination-bullet {
  position: relative;
  width: 6px;
  height: 6px;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  transition-property: opacity, background-color, transform;
  transition-duration: 0.2s, 0.1s;
  transition-timing-function: linear;
}

.slider__dots .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 12px;
  background-color: transparent;
  transform: translate(-50%, -50%);
}

.slider__dots .swiper-pagination-bullet:hover {
  background-color: #4764b0;
  transform: scale(1.5);
}

.slider__dots .swiper-pagination-bullet:active {
  opacity: 0.5;
}

.slider-arrows {
  display: none;
}

.slider-arrows__control {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 20px;
  height: 6px;
  margin: 0;
  padding: 0;
  border: none;
  border-bottom: 2px solid #000000;
  cursor: pointer;
  transition-property: opacity, border-color;
  transition-duration: 0.2s, 0.1s;
  transition-timing-function: linear;
}

.slider-arrows__control::before {
  content: "";
  position: absolute;
  top: 0;
  right: -1px;
  width: 5px;
  height: 5px;
  border: 3px solid #000000;
  border-top-color: transparent;
  border-right-color: transparent;
  transition-property: opacity, border-color;
  transition-duration: 0.2s, 0.1s;
  transition-timing-function: linear;
}

.slider-arrows__control::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 8px;
  background-color: transparent;
}

.slider-arrows__control:hover,
.slider-arrows__control:hover::before {
  border-color: #4764b0;
  border-top-color: transparent;
  border-right-color: transparent;
}

.slider-arrows__control:active,
.slider-arrows__control:active::before {
  opacity: 0.5;
  border-top-color: transparent;
  border-right-color: transparent;
}

.slider-arrows__control--prev {
  transform: rotate(-180deg);
}

