// site-list

.site-list {
	display: none;
	flex-wrap: wrap;
	align-items: center;
	.mp-reset();
	list-style: none;

	@media (min-width: @S-min) {
		display: flex;
	}
}

.site-list__link {
	// ! ie11 fallback
	display: block;
	width: 20px;
	height: 20px;
	background-image: url("../img/icon-globe.svg");
	background-size: cover;
	background-position: center;
	transition-property: fill, opacity;
	transition-duration: 0.2s, 0.1s;
	transition-timing-function: linear;

	&:hover {
		opacity: 0.5;
		fill: @page-blue;
	}

	&:active {
		opacity: 0.3;
	}
}
