// slider

.slider {
	position: relative;
}

.slider__list {
	.mp-reset();
	list-style: none;
}

// ! works block

.slider--works {
	margin-left: -20px;

	.slider__dots {
		bottom: 38px;
		left: 50%;
		width: 150px;
		transform: translateX(-50%);
	}

	.swiper-pagination-bullet {
		background-color: @basic-white;
	}

	.swiper-pagination-bullet-active {
		background-color: @page-blue;
		transform: scale(1.8);
	}

	@media (min-width: @S-min) {
		.slider__dots {
			bottom: 50px;
		}
	}

	@media (min-width: @L-min) {
		width: 920px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-left: -60px;
	}
}

// ! principles block

.slider--principles {
	width: calc(100% + 20px);
	margin-right: -20px;

	.slider__list {
		height: auto;
	}

	.slider__item {
		padding-top: 33px;
		padding-right: 20px;
		padding-bottom: 42px;
		padding-left: 20px;
		background-color: @basic-white;
	}

	.slider__dots {
		bottom: 20px;
		left: 50%;
		width: 70px;
		transform: translateX(-50%);
	}

	.swiper-pagination-bullet {
		background-color: @page-blue;

		&:hover {
			transform: scale(1.5);
		}
	}

	.swiper-pagination-bullet-active {
		transform: scale(1.8);
	}

	@media (min-width: @M-min) {
		width: calc(100% + 30px);
		margin-right: -30px;

		.slider__item {
			padding-top: 45px;
			padding-right: 30px;
			padding-bottom: 45px;
			padding-left: 50px;
		}
	}

	@media (min-width: @L-min) {
		width: calc(100% + 60px);
		min-height: 310px;

		.slider__item {
			padding-top: 44px;
			padding-right: 60px;
			padding-bottom: 54px;
		}
	}
}

// ! employees block

.slider--employees {
	width: calc(100% + 20px);
	margin-left: -20px;

	.slider__dots {
		left: 50%;
		bottom: 50px;
		width: 150px;
		transform: translate(-50%);
	}

	.swiper-pagination-bullet {
		background-color: @basic-white;
	}

	.swiper-pagination-bullet-active {
		background-color: @page-blue;
		transform: scale(1.8);
	}

	@media (min-width: @S-min) {
		.slider__dots {
			bottom: 70px;
		}
	}

	@media (min-width: @M-min) {
		width: calc(100% + 30px);
		margin-left: -30px;
	}

	@media (min-width: @L-min) {
		width: 800px;
		margin-left: -60px;
	}
}

.slider__img {
	width: 100%;
}

.slider__subtitle {
	position: relative;
	margin-bottom: 34px;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		bottom: -15px;
		width: 100%;
		height: 4px;
		background-color: @page-blue;
	}

	&::after {
		content: "";
		position: absolute;
		bottom: -15px;
		left: 6px;
		width: 6px;
		height: 4px;
		background-color: @basic-white;
		box-shadow: 22px 0 0 0 @basic-white, 48px 0 0 0 @basic-white,
			77px 0 0 0 @basic-white, 130px 0 0 0 @basic-white;
	}

	@media (min-width: @S-min) {
		&::after {
			box-shadow: 22px 0 0 0 @basic-white, 48px 0 0 0 @basic-white,
				77px 0 0 0 @basic-white, 130px 0 0 0 @basic-white,
				240px 0 0 0 @basic-white, 450px 0 0 0 @basic-white;
		}
	}

	@media (min-width: @M-min) {
		&::after {
			box-shadow: 22px 0 0 0 @basic-white, 48px 0 0 0 @basic-white,
				77px 0 0 0 @basic-white, 130px 0 0 0 @basic-white,
				240px 0 0 0 @basic-white, 450px 0 0 0 @basic-white,
				800px 0 0 0 @basic-white;
		}
	}

	@media (min-width: @L-min) {
		margin-bottom: 52px;
		padding-right: 20px;

		&::before,
		&::after {
			bottom: -17px;
		}

		&::after {
			width: 8px;
			left: 8px;
			box-shadow: 30px 0 0 0 @basic-white, 65px 0 0 0 @basic-white,
				104px 0 0 0 @basic-white, 175px 0 0 0 @basic-white,
				308px 0 0 0 @basic-white, 566px 0 0 0 @basic-white;
		}
	}
}

// * dots

.slider__dots {
	position: absolute;
	z-index: 101;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	min-height: 10px;
	.mp-reset();
	list-style: none;

	.swiper-pagination-bullet {
		position: relative;
		width: 6px;
		height: 6px;
		.mp-reset();
		border: none;
		border-radius: 100%;

		cursor: pointer;
		transition-property: opacity, background-color, transform;
		transition-duration: 0.2s, 0.1s;
		transition-timing-function: linear;

		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			width: 12px;
			height: 12px;
			background-color: transparent;
			transform: translate(-50%, -50%);
		}

		&:hover {
			background-color: @page-blue;
			transform: scale(1.5);
		}

		&:active {
			opacity: 0.5;
		}
	}

	@media (min-width: @L-min) {
		display: none;
	}
}
