// * arrows

.slider-arrows {
	display: none;

	@media (min-width: @L-min) {
		display: flex;
		flex-direction: column;
		align-self: center;
		justify-content: space-between;
		width: 20px;
		height: 18px;
	}
}

.slider-arrows__control {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	width: 20px;
	height: 6px;
	.mp-reset();
	border: none;
	border-bottom: 2px solid @basic-black;
	cursor: pointer;
	transition-property: opacity, border-color;
	transition-duration: 0.2s, 0.1s;
	transition-timing-function: linear;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: -1px;
		width: 5px;
		height: 5px;
		border: 3px solid @basic-black;
		border-top-color: transparent;
		border-right-color: transparent;
		transition-property: opacity, border-color;
		transition-duration: 0.2s, 0.1s;
		transition-timing-function: linear;
	}

	&::after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 20px;
		height: 8px;
		background-color: transparent;
	}

	&:hover,
	&:hover::before {
		border-color: @page-blue;
		border-top-color: transparent;
		border-right-color: transparent;
	}

	&:active,
	&:active::before {
		opacity: 0.5;
		border-top-color: transparent;
		border-right-color: transparent;
	}
}

.slider-arrows__control--prev {
	transform: rotate(-180deg);
}
