// company-now

.company-now {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 10px;
		width: 100%;
		height: 98.8%;
		border: 2px solid @page-dark-grey;
		border-right-color: transparent;
		border-top-color: transparent;
	}

	&::after {
		content: "";
		position: absolute;
		width: 12px;
		height: 2px;
		top: 0;
		left: 0;
		background-color: @page-dark-grey;
	}

	@media (min-width: @L-min) {
		&::before {
			top: 220px;
			left: 298px;
			height: 75.5%;
		}

		&::after {
			top: 220px;
			width: 298px;
		}
	}
}

.company-now__content {
	padding-top: 11px;
	padding-bottom: 34px;

	@media (min-width: @S-min) {
		padding-top: 38px;
		padding-bottom: 60px;
	}

	@media (min-width: @L-min) {
		padding-top: 16px;
		padding-bottom: 37px;
	}
}

.company-now__info {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: 34px;

	@media (min-width: @S-min) {
		align-items: flex-start;
		margin-bottom: 25px;
	}

	@media (min-width: @M-min) {
		margin-bottom: 57px;
	}

	@media (min-width: @L-min) {
		margin-bottom: 88px;
	}
}

.company-now__title {
	width: 100%;
	margin-bottom: 15px;
	margin-left: -2px;

	@media (min-width: @L-min) {
		margin-bottom: 17px;
		margin-left: 0;
	}
}

.company-now__text {
	width: 45%;
}

.company-now__br {
	display: none;

	@media (min-width: @L-min) {
		display: inline;
	}
}

.company-now__details {
	display: flex;
	flex-wrap: wrap;
	width: 46%;

	@media (min-width: @S-min) {
		width: 50%;
		justify-content: space-between;
	}

	@media (min-width: @L-min) {
		width: 546px;
	}
}

.company-now__item {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	width: 67%;
	max-width: 120px;
	margin-bottom: 12px;

	@media (min-width: @S-min) {
		width: 48%;
		max-width: none;
		margin-bottom: 0;
	}

	@media (min-width: @L-min) {
		width: 238px;
	}
}

.company-now__term {
	order: 1;
}

.company-now__def {
	margin-bottom: 3px;
	font-weight: 700;
	text-transform: uppercase;

	@media (min-width: @L-min) {
		margin-bottom: 1px;
	}
}

.company-now__bold {
	display: inline-block;
	margin-bottom: 6px;
	font-size: 36px;
	line-height: 0.85;
	font-weight: bold;

	@media (min-width: @L-min) {
		margin-bottom: 7px;
		font-size: 48px;
	}
}

.company-now__features {
	position: relative;
	z-index: 101;
	width: calc(100% + 20px);
	margin-left: -20px;
	padding-top: 24px;
	padding-right: 20px;
	padding-bottom: 27px;
	padding-left: 18px;
	background-color: @basic-white;

	@media (min-width: @S-min) {
		width: 55%;
	}

	@media (min-width: @M-min) {
		width: calc(50% + 30px);
		margin-left: -30px;
	}

	@media (min-width: @L-min) {
		width: 470px;
		margin-left: -60px;
		padding-top: 63px;
		padding-right: 40px;
		padding-bottom: 61px;
		padding-left: 59px;
	}
}

.company-now__image-wrap {
	margin-top: -30px;
	margin-right: -20px;

	@media (min-width: @S-min) {
		width: 65%;
		margin-top: -220px;
		margin-left: auto;
	}

	@media (min-width: @M-min) {
		margin-top: -260px;
	}

	@media (min-width: @L-min) {
		width: 920px;
		height: 570px;
		margin-top: -535px;
		margin-right: -60px;
	}
}

.company-now__img {
	width: 100%;
}
