// page-cloud-list

.page-cloud-list {
	.mp-reset();
	padding-left: 20px;
	list-style: none;

	@media (min-width: @L-min) {
		padding-left: 26px;
	}
}

.page-cloud-list__item {
	position: relative;
	margin-bottom: 8px;

	&:last-child {
		margin-bottom: 0;
	}

	&::before {
		content: "";
		position: absolute;
		top: 9px;
		left: -20px;
		width: 13px;
		height: 1px;
		background-color: @page-blue;
	}

	@media (min-width: @L-min) {
		margin-bottom: 10px;

		&::before {
			top: 12px;
			left: -25px;
			width: 15px;
			height: 2px;
		}
	}
}
